<template>
  <main class="register">
    <section class="page-title-section theme-bg">
        <div class="container">
            <div class="page-title">
                <h1>Register</h1>
                <ol class="breadcrumb justify-content-center">
                    <li><router-link to="/">Home</router-link></li>
                    <li class="active">Register</li>
                </ol>
            </div>
        </div>
    </section>

    <section>
      <div class="container container-large">
        <div class="row  sm-p-30px-b">
          <div class="col-md-8 offset-md-2 col-lg-8 offset-lg-2 right-img md-m-30px-t">
            <div class="contact-form my-3">
              <img  class="user-avatar-cred" src="../../assets/static/img/user-icon.svg" alt="user sign in">
              <form class="contactform py-3 my-3" v-on:submit.prevent="onSubmit">
                <div class="row">
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="firstName" v-model="user.firstName" type="text" placeholder="First Name"  class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.firstName">{{errors.firstName}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="lastName" v-model="user.lastName" type="text" placeholder="Last Name"  class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.lastName">{{errors.lastName}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="email" v-model="user.email" type="email" placeholder="Email" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.email">{{errors.email}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="phoneNumber" v-model="user.phoneNumber" type="text" placeholder="Phone Number" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.phoneNumber">{{errors.phoneNumber}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="password" v-model="user.password" type="password" placeholder="Password" name="password" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.password">{{errors.password}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="confirmPassword" v-model="user.confirmPassword" type="password" placeholder="Confirm Password" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.confirmPassword">{{errors.confirmPassword}}</div>
                        </div>
                    </div>

                    <div class="col-md-12">
                        <p class="mb-0">Register as a?</p>
                        <div class="row mb-3">
                            <div class="col-md-6">
                                <div class="custom-control custom-radio">
                                    <input type="radio" id="meterOnwerSelect" v-model="user.accountType" class="custom-control-input" value="1315">
                                    <div class="invalid-feedback block" v-if="errors.accountType">{{errors.accountType}}</div>
                                    <label class="custom-control-label" for="meterOnwerSelect">Meter Owner</label>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="custom-control custom-radio inline-block">
                                    <input type="radio" id="estateAdminSelect" v-model="user.accountType" class="custom-control-input" value="51">
                                    <label class="custom-control-label" for="estateAdminSelect">Estate Admin</label>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                    <div class="col-md-12">
                        <div class="row" v-if="user.accountType == 1315">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <input id="meterNumber" v-model="user.meterNumber" type="number" min="0" placeholder="Meter Number" class="form-control" required>
                                    <div class="invalid-feedback block" v-if="errors.meterNumber">{{errors.meterNumber}}</div>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <select v-model="user.meterType" id="meterType" class="form-control">   
                                        <option value="">Meter Type</option>
                                        <option value="water">Water</option>
                                        <option value="electricity">Electricity</option>
                                        <option value="gas">Gas</option>
                                    </select>
                                    <div class="invalid-feedback block" v-if="errors.meterType">{{errors.meterType}}</div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="col-md-12">
                        <div class="send">
                            <button class="btn btn-primary" type="submit" name="send"> Register</button>
                        </div>
                        <div class="clearfix"></div> <br/>
                        <p class="gray-dark">Already have account? <router-link to="login">Login</router-link> </p>
                    </div>
                </div>
              </form>
            </div>
          </div>
        </div> 
      </div> 
    </section>
    <FlashMessage></FlashMessage>
  </main>
</template>

<script>
import * as auth from "../../services/auth_service";
import * as sendSMS from "../../services/sms_service";
import * as sendEmail from "../../services/email_service";

export default {
  name: "register",
  data() {
    return {
      user: {
        firstName: "",
        lastName: "",
        email: "",
        phoneNumber: "",
        password: "",
        confirmPassword: "",
        accountType: 1315,
        meterNumber: "",
        meterType: ""
      },
        smsMessageData:{
                    message:'',
                    phoneNumber:'',
                    messageType:'UserRegistration'
                },
        emailMessageData:{
                    message:'',
                    email:'',
                    messageType:'UserRegistration'
                },
      errors: {},
      message: ""
    };
  },
  methods: {
    onSubmit: async function() {
      await auth
        .registerUser(this.user)
        .then(response => {
          this.errors = {};
          this.$store.state.commonMessage = "registration";
          try {
            this.smsMessageData.message="Hello "+this.user.firstName +". Thank you for registering on Vendr. \n Welcomem to a pleasant experience. "
            this.smsMessageData.phoneNumber = this.user.phoneNumber
            this.smsMessageData.messageType="UserRegistration"
            this.sendSMS(this.smsMessageData);
            
            this.emailMessageData.email=this.user.email
            this.emailMessageData.message="Hello "+this.user.firstName +". Thank you for registering on Vendr. \n Welcomem to a pleasant experience. "

            this.sendEmail(this.emailMessageData);
          } catch (error) {
            this.flashMessage.error({
              title: "SMS not sent for " + this.$store.state.userPhoneNumber,
              message:
                "An admin has been notified about this issue! Please ensure your Number is correct or contact admin",
              time: 8000,
              icon: "../../assets/static/img/water_drop.svg"
            });
          }
          this.$router.push({ name: "login" });
        })
        .catch(error => {
          console.log(error.response.data);
          if (error.response.data.message) {
            this.errors = {};
            this.flashMessage.error({
              title: "Error",
              message: "Error occured please reload the page and try again.",
              time: 5000,
              icon: "/img/user-dummy.52069854.jpg"
            });
          } else {
            this.errors = error.response.data.errors;
          }
        });
    },
    sendSMS: async function() {
      try {
        //console.log("In try Statement")

        await sendSMS.sendSMS(this.smsMessageData);
      } catch (error) {
        console.log(error);
        this.flashMessage.error({
          title: "SMS Not sent",
          message: "Some error occured, contact admin!",
          time: 5000,
          icon: "../../assets/static/img/water_drop.svg"
        });
      }
    },
     sendEmail: async function() {
      try {
        //console.log("In try Statement")

        await sendEmail.sendEmail(this.emailMessageData);
      } catch (error) {
        console.log(error);
        this.flashMessage.error({
          title: "Email Not sent",
          message: "Some error occured, contact admin!",
          time: 5000,
          icon: "../../assets/static/img/water_drop.svg"
        });
      }
    }

  }
};
</script>
